.nav-phone{
    float: right;
    font-size: 18px;
}

.sidebar .user-info {
    padding: 13px 15px 0 15px;
    background: none;
    height: 180px;
    text-align: center;
    background-color: #263238;
    border-bottom: none;
}

.sidebar .menu {
    background-color: #37474F;
    color: #fff;
}

.sidebar .menu .list li.active > :first-child i, .sidebar .menu .list li.active > :first-child span {
    color: #FF1744;
}

.sidebar .menu .list li > :first-child i, .sidebar .menu .list li > :first-child span {
    color: #fff;
}

.card .body{
    padding: 5px;
}

.card-custom{
    margin: 5px;
}

.card-shadow:hover {
    -webkit-box-shadow: 0 10px 15px rgba(0,0,0,.5);
    box-shadow: 0 10px 15px rgba(0,0,0,.5);
    cursor: pointer;
}

.card-stars > i{
    color: #FF1744;
    font-size: 18px;
}

.card-value{
    color: #FF1744;
    font-size: 16px;
}

@media (min-width: 1170px){
    .navbar>.container .navbar-brand, .navbar>.container-fluid .navbar-brand {
        color: transparent;
    }
}

section.content {
    margin: 70px 15px 0 315px;
}

.carousel{
    z-index: 0
}

.panel {
    -webkit-box-shadow: 0 2px 5px rgba(0,0,0,.16), 0 2px 10px rgba(0,0,0,.12);
            box-shadow: 0 2px 5px rgba(0,0,0,.16), 0 2px 10px rgba(0,0,0,.12);
    border-radius: 2px;
}

.btn:not(.btn-link):not(.btn-circle):hover {
    outline: none;
    color: white;
}


